import Component from "vue-class-component";
import axios from "axios";
import * as indexApi from "../../api/indexApi";
import  remindertable from "./remindertable";
import moment from "moment";

@Component({
  props: {},
  components: {
  remindertable
  }
})
export default class AddGoods extends Vue {

handleOrderStatus(status){
switch(status){
  case 'ORDER_INIT':
  return '订单初始化';
   case 'ORDER_WAIT_PAY':
  return '等待支付';
   case 'ORDER_PAY_ONDEV':
  return '货到付款';
   case 'ORDER_CANCEL_PAY':
  return '取消支付';
   case 'ORDER_WAIT_SENDGOODS':
  return '待发货';
     case 'ORDER_WAIT_RECVGOODS':
  return '待收货';
  case 'ORDER_END_GOODS':
  return '完成';
  default :
  return '';
}
}
paymentMode =''
reminderVEnums = 'NEW_ORDER'
  allprescription = 0;
 loading = false
  getOrderExcel() {
    this.loading = true;

    let startCreatTime = "";
    let endCreatTime = "";
    if ((this.startDate || "") != "") {
      startCreatTime =
        moment(this.startDate).format("YYYY-MM-DD") + " 00:00:00";
    }
    if ((this.endDate || "") != "") {
      endCreatTime = moment(this.endDate).format("YYYY-MM-DD") + " 23:59:59";
    }
    
    indexApi.getExcelUrl({
  startCreatTime: startCreatTime,
        endCreatTime: endCreatTime,
        rderStatus: this.rderStatus,}).then(res => {
      if (res["retCode"]) {
        if (res.data.del && res.data.del.info == "sucess") {
          this.doResult(res.data.durl);
        } else {
        this.loading = false;
          this.$alert("暂无可导出数据");
        }
      } else {
        if (!res["islogin"]) {
          this.$alert(res["message"]);
        }
      }
    });
  }
durl =''
doDownLoad(){
  window.open(this.durl);
}
  doResult(durl) {

    indexApi.orderDoResult({}).then(res => {
        this.loading = false;
      if (res["retCode"]) {
        this.$message("导出成功");
          this.durl = durl
          // sessionStorage
          // 塞到缓存
      } else {
        if (!res["islogin"]) {
          this.$alert(res["message"]);
        }
      }
    });
  }
  key = ''
  page = 0;
  pageSize = 10;
  total = 100;
  onPageChange(page) {
    this.page = page - 1;
    this.getOrderList();
  }
  orderList = [];
    startDate = "";
  endDate = "";

consigneeAddress =''
   handleClick(e) {
     this.rderStatus = ''
    this.getOrderList();
  }
  rderStatus=''
  getOrderList() {
  let startCreatTime = "";
    let endCreatTime = "";
    if ((this.startDate || "") != "") {
      startCreatTime =
        moment(this.startDate).format("YYYY-MM-DD")  + " 00:00:00";
    }
    if ((this.endDate || "") != "") {
      endCreatTime = moment(this.endDate).format("YYYY-MM-DD")  + " 23:59:59";
    }
this.reminderVEnums == 'ALL'
    indexApi
      .gerOrderList({
                paymentMode:this.paymentMode,
        reminderVEnums:this.reminderVEnums,
        key:this.key,
        consigneeAddress:this.consigneeAddress,
     startCreatTime: startCreatTime,
        endCreatTime: endCreatTime,
        rderStatus : this.rderStatus,
        page: this.page,
        pageSize: this.pageSize
      })
      .then(res => {
        if (res["retCode"]) {
          console.log('-----',res.data)
          this.orderList = res.data.data;
          this.total = res.data.page.total;
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}
          console.error("数据查询错误");
        }
      });
  }
NEW_ORDER = 0
queryOrderCount(){
   indexApi
      .queryOrderCount({
      })
      .then(res => {
   
        if (res["retCode"]) {
            this.NEW_ORDER = res.data.NEW_ORDER
   this.allprescription = res.data.all; 

        } else {
          if(!res['islogin']){this.$alert(res["message"]);}
          console.error("数据查询错误");
        }
      });
}

  mounted() {
    this.queryOrderCount()
    // this.allPrescription();
    this.getOrderList();
  }
}



